import { Button } from 'reactstrap';
import Form from 'react-bootstrap/Form';
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { logIn } from '../../../redux/usersRedux';
import { useNavigate } from 'react-router-dom';
import { API_URL } from '../../../config';
import styles from './Login.module.scss';

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [status, setStatus] = useState(null); // null, 'loading, 'success', 'serverError', 'emailError'
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();

    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'include',
      body: JSON.stringify({ email, password }),
    };

    setStatus('loading');
    fetch(`${API_URL}auth/login`, options)
      .then((res) => {
        if (res.status === 201) {
          setStatus('success');
          dispatch(logIn({ email }));
          setTimeout(() => {
            navigate('/');
          }, 1500);
        } else if (res.status === 400) {
          setStatus('clientError');
        } else {
          setStatus('serverError');
        }
      })
      .catch((error) => {
        setStatus('Error:', error);
      });
  };

  return (
    <Form className="col-12 col-sm-3 mx-auto" onSubmit={handleSubmit}>
      <h2 className="my-4">Zaloguj si</h2>
      {status === 'success' && (
        <Alert variant="success">
          <Alert.Heading>Sukces!</Alert.Heading>
          <p>Udao Ci si zalogowa!</p>
        </Alert>
      )}
      {status === 'serverError' && (
        <Alert variant="danger">
          <Alert.Heading>Co poszo nie tak...</Alert.Heading>
          <p>Sprbuj ponownie!</p>
        </Alert>
      )}
      {status === 'clientError' && (
        <Alert variant="danger">
          <Alert.Heading>Nieprawidowe dane</Alert.Heading>
          <p>Email lub haso s nieprawidowe...</p>
        </Alert>
      )}
      {status === 'loading' && (
        <Spinner animation="border" role="status" className="block mx-auto">
          <span className="visually-hidden">Oczekiwanie...</span>
        </Spinner>
      )}
      <Form.Group className="my-3" controlId="formEmail">
        <Form.Control
          type="text"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="Email"
        />
      </Form.Group>
      <Form.Group className="mb-3" controlId="formPassword">
        <Form.Control
          type="password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="Haso"
        />
      </Form.Group>
      <Button color="success" className={styles.btnLogin}>
        <b>Zaloguj</b>
      </Button>
      <p>
        Nie pamitasz hasa?
        <b>
          <a href="/lost-account">
            <b> Kliknij tutaj!</b>
          </a>
        </b>
      </p>
      <p>
        Nie masz konta?
        <b>
          <a href="/register"> Zarejestruj si!</a>
        </b>
      </p>
    </Form>
  );
};

export default Login;
